.board {
    border-collapse: collapse;
}

.board table {
    border: 1px solid gray;
    width: 100%;
    table-layout: fixed;
}


.board td, .board th{
    border: 1px solid gray;
    width: 10%; /* Adjust this value to set the width of each column */
}

button {
    margin: 2em;
    height: 2em;
    background-color: white;
    border: none;
    font-size: 2em;
    font-weight: bold;
    /*transition-duration: 0.4s;*/
}

button:hover {
    background-color: lightgray;
}

button:active {
    color: yellow;
}